import React, { useState, useEffect } from 'react';
import useGet from '../Hooks/Get';
import SingleRefund from '../Components/SingleRefund';
import Select from 'react-select';
import MultipleRefund from '../Components/MultipleRefund';

const RefundInitiator = () => {
  const mainFilteroption = [
    'Single' ,'Multiple' 
  ];

  const [selectedMainFO, setSelectedMainFO] = useState(mainFilteroption[0]);
 useEffect(()=>{
  setSelectedMainFO(mainFilteroption[0])
 },[])
 

  return (
    <div className="flex flex-col w-full h-full space-y-8">
      <div className="h-16 w-full flex justify-center items-center shadow-lg">
        <p className="text-xl font-bold">Refund Initiator</p>
      </div>
      <div className="flex flex-col px-8">
        <div className="h-12">
        <div className='flex  space-x-2'>
        <p className={`text-xl font-semibold cursor-pointer ${selectedMainFO == mainFilteroption[0] ?'text-black font-bold':'text-gray-600'}`} onClick={()=>{setSelectedMainFO(mainFilteroption[0])}}>{mainFilteroption[0]}</p>
        <p className='text-xl font-semibold'>/</p>
        <p className={`text-xl font-semibold cursor-pointer ${selectedMainFO == mainFilteroption[1] ?'text-black font-bold':'text-gray-600'}`} onClick={()=>{setSelectedMainFO(mainFilteroption[1])}}>{mainFilteroption[1]}</p>
        </div>
        </div>
        
        {selectedMainFO && selectedMainFO === 'Single' ? <SingleRefund /> : <MultipleRefund/>}
      </div>
    </div>
  );
};

export default RefundInitiator;
