import React, { useEffect, useState } from 'react';
import Select from 'react-select';
import useGet from '../Hooks/Get';

const BootcampSelect = ({ selectedBootcamp, setSelectedBootcamp, isMulti = false }) => {
  const [options, setOptions] = useState([]);

  const url = `/bootcamps/bootcamp?id=0`;
  const { data: bootcampData = [] } = useGet(url);

  useEffect(() => {
    if (bootcampData) {
      const formattedOptions = bootcampData.map((i) => ({
        label: i.title,
        value: i.id,
      }));
      setOptions(formattedOptions);
      if (!selectedBootcamp && formattedOptions.length > 0 && !isMulti) {
        setSelectedBootcamp(formattedOptions[0]);
      }
    }
  }, [bootcampData, selectedBootcamp, isMulti]);

  const handleChange = (selectedOption) => {
    setSelectedBootcamp(selectedOption);
  };

  return (
    <Select
      className="w-64 h-8"
      name="bootcamp"
      value={selectedBootcamp}
      options={options}
      isMulti={isMulti}
      placeholder="Bootcamp Name"
      onChange={handleChange}
    />
  );
};

export default BootcampSelect;
