import React, { useEffect, useMemo, useState } from 'react';
import Select from 'react-select';
import useGet from '../Hooks/Get';
import BootcampSelect from './BootcampSelect';
import DynamicTable from './DynamicTable';
import usePost from '../Hooks/UsePost';
import { showPopup } from '../../Components/Notification';
import { TiTick } from "react-icons/ti";
import useCreateExcelFile from './CreateExcelFile';
const MultipleRefund = () => {
  const [bootcamps, setBootcamps] = useState([]);
  const [bootcampData, setBootcampData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [fetchTrigger,setFetchTrigger]=useState(0)
  const [selectedIds, setSelectedIds] = useState([]);
  const [selectAll, setSelectAll] = useState(false);
  const [requestedRefunds,setReq] = useState(0)
  const [success,setsuccess] = useState(0)
  const {downloadExcelFile,downloading} = useCreateExcelFile('refundAll_data');
  const [refundStatusData,setRefundStatusData] = useState([])
  const bootcampUrl = useMemo(()=>{
    setReq(0)
    if(bootcamps.length <1){
        return null
    }
   const bootcampIds= bootcamps.map(bootcamp => bootcamp.value).join(',')
    return `/refundInitiator/getData?bootcampIds=${bootcampIds}`
  },[fetchTrigger])

  const { data, loading: fetchLoading, error: fetchError } = useGet(bootcampUrl);
  const {post,loading:refundPostLoading,error:refundPostError} = usePost(`/refundInitiator/refundInitiateAll`)
  

  
  useEffect(() => {
    setReq(0)
    if (selectAll && bootcampData) {
      const ids = bootcampData.map((content) => content.id);
      setSelectedIds(ids);
    } else {
      setSelectedIds([]);
    }
  }, [selectAll]);
  useEffect(() => {
    setRefundStatusData([])
    setSelectAll(false)
    if (data) {
      setBootcampData(data);
      setLoading(fetchLoading);
      setError(fetchError);
    }
  }, [data, fetchLoading, fetchError]);
 
  const handleCheckboxChange = (id) => {
    setReq(0)
    setSelectedIds((prevSelectedIds) =>
      prevSelectedIds.includes(id)
        ? prevSelectedIds.filter((selectedId) => selectedId !== id)
        : [...prevSelectedIds, id]
    );
  };
  const initiateRefundAll =async()=>{
    setReq(0)
    const userDetails  = bootcampData.filter((content)=>
        selectedIds.includes(content.id)
    ).map((content)=>({...content ,razorPayID:content.paymentid}))
    if(userDetails.length > 0){
        console.log(userDetails)
        try {
            const output = await post({userDetails});
            const {results}=output;
            console.log(results)
            const noOfsuccess = results.reduce((acc,item)=>Number(acc)+Number(item.status == 'success'?1:0),0);
            setsuccess(noOfsuccess);
            setReq(results.length);
            setRefundStatusData(results)


        } catch (error) {
            showPopup({success:false,message:'an error occured'})
            console.log(error)
            setReq(0)
            setRefundStatusData([])
        }
    }
  }
 
  const handleSelectAllChange = () => {
    setReq(0)
    setRefundStatusData([])
    setSelectAll((prevSelectAll) => !prevSelectAll);
  };
  return (
    <div className='flex flex-col space-y-8'>
      <div className='flex space-x-4 items-start'>
  <BootcampSelect selectedBootcamp={bootcamps} setSelectedBootcamp={setBootcamps} isMulti={true} />

  <button
    className={`px-5 py-2 bg-black text-white rounded border-2 hover:bg-gray-700 ${loading ? 'opacity-50 cursor-not-allowed' : ''}`}
    onClick={() => setFetchTrigger(fetchTrigger + 1)} 
    disabled={loading}
  >
    {loading ? 'Fetching' : 'Fetch Data'}
  </button>

  {selectedIds && selectedIds.length > 0 && (
    <button
      className='px-5 py-2 bg-green-600 text-white rounded border-2 border-green-600 hover:bg-green-400'
      onClick={() => initiateRefundAll()}
      disabled={refundPostLoading}
    >
      {refundPostLoading ? 'Refunding' : 'Refund All'}
    </button>
  )}

  {requestedRefunds !== 0 && (
    <div className='flex flex-col space-y-2 h-full'>
      <p>Processed Refund Requests: {requestedRefunds}</p>
      <p className='flex space-x-2'>
        <p>Number of Refunds Success: {success}</p>
         <TiTick className='text-green-600 text-xl' />
      </p>
      <p className='flex space-x-2 items-center h-4 '>
        <p className=''>Number of Refunds Failed: {requestedRefunds - success}</p>
        <p className='text-lg font-semibold text-red-600 '>X</p>
      </p>
    </div>
  )}
  {
    refundStatusData && refundStatusData.length > 0 && 
    <button
     className='px-5 py-2 rounded-md hover:bg-gray-700 border-2 text-white bg-black' disabled={downloading} 
     onClick={async()=>{downloadExcelFile(refundStatusData)}}>Download record</button>
  }
</div>
<div>
      {
        bootcampData && bootcampData.length > 0 && <DynamicTable data={bootcampData}  component={(row) => (
            <input
              className='cursor-pointer'
              type="checkbox"
              checked={selectedIds.includes(row.id)}
              onChange={() => handleCheckboxChange(row.id)}
            />
          )}
          action={
            <input
              type="checkbox"
              checked={selectAll}
              onChange={handleSelectAllChange}
            />
          }/>
      }

      </div>
     
    </div>
  );
};

export default MultipleRefund;
