
import DynamicTable from '../Components/DynamicTable';
import { showPopup } from '../../Components/Notification';
import usePost from '../Hooks/UsePost';
import React, { useState, useEffect,useMemo, Suspense } from 'react';
import useGet from '../Hooks/Get';
const SingleRefund = () => {
    
    const [razorPayID, setRazorPayID] = useState('');
    const [razorPaydata,setRazorPaydata] = useState('')
    const [details,setdetails]=useState();
    const [isInvalid,setInvalid]=useState(false)
    const {post,loading:refundPostLoading,error:refundPostError} = usePost(`/refundInitiator/refundInitiate`)

  
  const checkurl = useMemo(()=>{
    if(razorPaydata.trim() == ""){
        return null
    }
  
      
    return `/refundInitiator/checkData?razorPayID=${razorPayID}`
  },[razorPaydata])
  console.log(checkurl)
  const { data, loading, error } = useGet(checkurl);
  
  const handleCheck =  () => {
    if (!razorPayID) {
      return;
    }
    
    setRazorPaydata(razorPayID);
   
   
    
  };
 
 const initiateRefund =async()=>{
  const formData = new FormData();
    console.log(details[0].paymentid)
    formData.append('name', details[0].name || '');
    formData.append('id', details[0].id || '');
    formData.append('phone', details[0].phone || '');
    formData.append('email', details[0].email || '');
    formData.append('razorPayID', details[0].paymentid || '');
  try {
    const result = await post(formData);
    showPopup({success:true,message:result.message})
  } catch (error) {
    console.log(error)
    const errorMessage = (error.response && error.response.data && error.response.data.message)
      ? error.response.data.message
      : 'An unknown error occurred';

    showPopup({ success: false, message: errorMessage });
  }
 }
 const renderAction = ()=>{
  return (
    <button className='px-5 py-2 text-white bg-green-600 rounded-md' disabled={refundPostLoading} onClick={()=>initiateRefund()}>Initiate Refund</button>
  )
 }
  useEffect(() => {
   
    if (data && data.length>0) {
      setInvalid(false)
     setdetails(data)
    }else{
   setInvalid(true)
   setdetails([])
   setRazorPaydata('')
    }
   
  }, [data]);
  useEffect(()=>{
    setdetails([])
  },[error])
  return (
    <div>
         <div className="flex space-x-4 items-center">
          <label className="text-lg font-semibold">Payment ID:</label>
          <input
            type="text"
            value={razorPayID}
            onChange={(e) => setRazorPayID(e.target.value)}
            required
            className="px-5 py-2 border-2 border-gray-400 outline-none focus:border-2 focus:border-gray-500 rounded-md"
            placeholder="Enter the payment ID"
          />
          <button
            className="px-5 py-2 bg-black hover:bg-gray-700 text-white rounded-md border-2 border-black"
            onClick={handleCheck}
          >
            Check Valid
          </button>
          {
            isInvalid?<p></p>:''
          }
        </div>
        
        {loading && <p>Loading...</p>}
       
        {details && details.length > 0 && (
          <DynamicTable data={details} component={renderAction} action={'Action'} />
        )}
        {
          isInvalid  && razorPaydata && <>no matching data found</>
        }
    </div>
  )
}

export default SingleRefund